 .gpt3__blog-container_article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
 }

 .gpt3__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-footer);
 }

 .gpt3__blog-container_article-image img {
    width: 100%;
    height: 100%;
 }

 .gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
 }

 .gpt3__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
    color: #ffffff;
 }

 .gpt3__blog-container_article-content h3 {
    font-family: Manrope;
    font-size: 25px;
    font-weight: 800;
    line-height: 30px;
    color: #fff;

    margin-bottom: 5rem;
    cursor: pointer;
 }

 .gpt3__blog-container_article-content p:last-child{
    font-family: Manrope;
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
    color: #fff; 
 }

 @media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
 }